.App {
  text-align: center;
  /* background: black; */
  background: -webkit-gradient(linear, left top, left bottom, color-stop(25%, black), to(rgb(19, 19, 19)));
  background: -webkit-linear-gradient(black 25%, rgb(19, 19, 19) 100%);
  background: -o-linear-gradient(black 25%, rgb(19, 19, 19) 100%);
  background: linear-gradient(black 25%, rgb(19, 19, 19) 100%);
  min-height: 100vh;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  font-family: 'Press Start 2P', sans-serif;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  /* background-color: #222; */
  height: 93px;
  padding: 20px;
  color: yellow;
  margin-bottom: 15px;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

.images-row {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

@media only screen and (max-width: 400px) {
  .App-rules {
    font-size: 7px; 
  }
}

@media only screen and (max-width: 600px) {
  .App-rules {
    font-size: 8px; 
  }
}


