.image-container {
  background: black;
  /* border-radius: 2px; */
  /* box-shadow: 0 3px 6px #999, 0 3px 6px #999; */
  text-align: left;
  padding: 5px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;  
}

.image-container>.image {
  overflow: hidden;
  text-align: center;

}

.image-container>.image {
  width: 250px;   
}

@media only screen and (max-width: 600px) {
  .image-container>.image {
    width: 100px;  
  }
}

img {
  cursor: pointer;
  border-radius: 5px;
  width: 100%;
}


